
@charset "utf-8";
@import url('https://fonts.googleapis.com/css?family=DM+Sans:400,700&display=swap');
$family-sans-serif: 'DM Sans', sans-serif;


$orange: #fabf7f;
$green: #8df2a4;


.total {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    border: none;
    background: $orange; }

.winbg {
    background: $green; }


.challenge {
    width: 100vw;
    height: 30vh;
    display: flex;
    justify-content: center;
    align-items: flex-end; }


.challenge h1 {
    font-size: 50px;
    font-weight: 700;
    color: white; }



.noteboard {
    width: 100vw;
    height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    background: url("./bg-tile.jpg");
    box-sizing: border-box;
    padding: 4vh 5vw 12vh 5vw; }


.noteboardbtn {
    height: 70px;
    width: 70px;
    background: $orange;
    border: none;
    border-radius: 50%;
    margin: 5px;
    outline: none;
    transition: all .05s ease-in-out;
    -webkit-box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.25);
    -moz-box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.25);
    box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.25);
    font-size: 26px;
    font-weight: 700;
    color: white;
    text-align: center; }

.noteboardbtn:active {
    height: 70px;
    width: 70px;
    background: $orange;
    border: none;
    border-radius: 50%;
    margin: 5px;
    outline: none;
    transform: scale(0.9,0.9); }

.toggled {
    background: white;
    color: $orange; }

.winnoteboard {
 }    // background: url("./bg-tile2.jpg");

.iconholder {
    width: 100vw;
    height: 20vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start; }

.icon {
    display: block;
    width: 50px;
    height: 50px;
    opacity: 1;
    margin-top: 0px;
    transition: all 0.15s ease-in-out 0s; }

.hide {
    width: 50px;
    height: 50px;
    opacity: 0;
    margin-top: 20px; }

.gone {
    display: none; }



